html {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
        'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
#root {
    background: #F6F6F6;

}
.carousel-component .carousel-indicators li {
    background-color: #ec4090;
    width: 5px;
    height: 5px;
    border-radius: 100%;
}

.carousel-component .carousel-caption h3 {
    font-size: 1.5rem;
    font-weight: 300;
    color: white;
    background-color: rgba(236, 64, 144, 0.3);

    -webkit-animation-name: example;

            animation-name: example;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}
@-webkit-keyframes example {
    0% {
        transform: translateY(-1000%);
    }

    100% {
        transform: translateY(0);
    }
}
@keyframes example {
    0% {
        transform: translateY(-1000%);
    }

    100% {
        transform: translateY(0);
    }
}

@media (max-width: 800px) {
    .carousel-component .carousel-caption h3 {
        font-size: 1.5rem;
    }
}

.carousel-component .carousel-caption h3 {
    transform: translateX(0);
}

.parallax {
    position: relative;

    /* The image used */
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
        url(/RitualBeauty/static/media/img1920x900.4d80507e.jpg);

    /* Set a specific height */
    height: 444px;

    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.parallax .parallax-content {
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

@media (max-width: 450px) {
    .parallax .parallax-content p{
        font-size: 16px;
    }

    .parallax .parallax-content h2{
        font-size: 25px;
    }
}
.ServicesButton.btn.btn-outline-danger {
    color: #ec4090;
    border-radius: 10rem;
    border-color: #ec4090;
    padding: 10px 16px;
    opacity: 0.9;
}

.ServicesButton.btn.btn-outline-danger:hover,
.ServicesButton.btn.btn-outline-danger:focus {
    color: white;
    background-color: #ec4090;
    border-color: #ec4090;
    box-shadow: none;
    border-color: #ec4090;
    opacity: 1;
}

.ServicesButton.btn-outline-danger:not(:disabled):not(.disabled):active {
    background-color: #ec4090;
    border-color: #ec4090;
}

.services {
    padding: 80px 0;
    background: #f6f6f6;
    color: #272727;
    text-align: center;
}
.section-title {
    text-align: center;
    width: 90%;
    margin: 0 auto;
    padding-bottom: 60px;
}

.section-title h2 {
    letter-spacing: 5px;
    margin: 0;
    font-size: 2em;
    text-transform: uppercase;
}
.section-title h2:after {
    content: '';
    display: block;
    width: 10%;
    margin: 10px auto;
    border-bottom: solid #cadbe7;
}
.section-title p {
    width: 50%;
    margin: 0 auto;
    line-height: 25px;
}

@media (max-width: 768px) {
    .section-title p {
        width: 100%;
    }
}
.testimonials {
    padding: 100px 0 25px;
    background: #f6f6f6;
    color: #272727;
}

.testimonials-card {
    position: relative;
    min-height: 200px;
}

.testimonials-card img {
    position: absolute;
    border-radius: 50%;
    max-width: 100px;
    height: auto;
    margin: 0 auto;
}
.testimonials-info {
    margin-left: 115px;
}
.testimonials-card p {
    font-style: italic;
}
.testimonials-card footer {
    font-weight: 700;
    text-transform: uppercase;
}
.testimonials-card span {
    display: block;
    font-size: 16px;
    margin-right: 15px;
    color: #EC4090;
    text-transform: none;
}

@media (max-width: 500px) {
    .testimonials-card {
        text-align: center;
        padding-bottom: 30px;
    }
    .testimonials-card img {
        display: block;
        position: relative;
        margin: 0 auto;
    }
    .testimonials-info {
        margin-left: 0;
    }
}

