.header-inner {
    position: relative;
    width: 100%;
    height: 300px;
    padding: 0px;
    margin: 0px;
    z-index: 0;
    overflow: hidden;
    margin-top: 80px;
}
.header-inner .overlay {
    position: absolute;
    z-index: 1;
}
.header-inner .inner {
    position: absolute;
    width: 100%;
    padding: 0;
    margin: 0px;
    z-index: 3;
}
.header-inner {
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/RitualBeauty/static/media/header-img.8cf07ac2.jpg) center top no-repeat;
    width: 100%;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: rgba(0, 0, 0, 0.5);
}
/*inner page header title
 ------------------------------ */
.header-inner .title {
    padding: 100px 0 0 0;
    margin: 0px;
    font-size: 30px;
    font-weight: 700;
}
/*inner page header title 2
 ------------------------------ */
.header-inner .title-2 {
    padding: 133px 0 0 0;
    margin: 0px;
    font-size: 30px;
    font-weight: 700;
}

.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 36px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button-line {
    width: 30px;
    height: 2px;
    background: white;
}

.toolbar {
    position: fixed;
    width: 100%;
    background: #ec4090;
    height: 80px;
    top: 0;
    left: 0;
    z-index: 8000;
}

.toolbar-logo {
    margin-left: 1rem;
}
.toolbar-navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar-logo a {
    color: white;
    font-size: 1.2rem;
}
.toolbar-logo a:hover,
.toolbar-logo a:active {
    color: white;
}

.spacer {
    flex: 1 1;
}

.toolbar-navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.toolbar-navigation-items li {
    padding: 0 0.2rem;
}

.toolbar-navigation-items a {
    color: white;
    opacity: 0.8;
    text-transform: uppercase;
    font-size: small;
}
.toolbar-navigation-items a:hover,
.toolbar-navigation-items a:active {
    opacity: 1;
    color: white;
}

@media (max-width: 768px) {
    .toolbar-navigation-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .toolbar-toggle-button {
        display: none;
    }
    .toolbar-logo {
        margin-left: 0;
    }
}

.side-drawer {
    height: 100%;
    background: #EC4090;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 9999;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}
.side-drawer.open {
    transform: translateX(0);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.side-drawer li {
    margin: 0.5rem 0;
}

.side-drawer a {
    color: white;
    opacity: 0.7;
    font-size: 1.2rem;
    text-transform: uppercase;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: white;
    opacity: 1;
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}

.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 9000;
}

.footer {
    position: relative;
    overflow: hidden;
    display: block;
    padding-top: 60px;
    background-color: #101010;
    color: white;
    border-top: 5px solid #EC4090;
}

.footer-top {
    padding-bottom: 60px;
}

.footer-bottom {
    padding: 40px 0 40px 0;
    background-color: #1a1a1a;
    border-top: 1px solid #1a1a1a;
}

.footer-top i {
    margin-bottom: 10px;
    color: #ec4090;
    font-size: 60px;
}

.footer p {
    margin-bottom: 5px;
    line-height: 24px;
    color: #727272;
}

.footer p > a {
    font-size: 17px;
    color: #ec4090;
}

.footer p > a:hover {
    color: #ec4090;
}

span.template-name {
    font-weight: bold;
    color: #ec4090;
}
.footer .footer-title {
    margin: 10px 0;
    margin-top: 30px;
    color: #ec4090;
}

a.footer-links {
    display: block;
    font-size: 17px;
    margin-bottom: 2px;
    color: #727272;
}

a.footer-links:hover {
    color: #ec4090;
    text-decoration: underline;
    transition: all 0.5s ease-in-out;
}

/* Footer Social Icons */
ul.footer_social {
    width: 100%;
    max-width: 1140px;
    display: block;
    margin: 0 auto;
    padding: 0;
    padding-top: 15px;
    text-align: center;
}

ul.footer_social li {
    display: inline-block;
    margin: 0;
    line-height: 100%;
}

ul.footer_social li a .i {
    display: inline-block;
    margin: 0 8px;
    padding: 0;
    font-size: 26px;
    line-height: 26px;
    color: #1e90ff;
    transition: all 0.5s ease-in-out;
    -moz-transition: all 0.25s ease-in-out;
    -webkit-transition: all 0.25s ease-in-out;
}

ul.footer_social li a .fa-twitter {
    color: #4099ff;
}

ul.footer_social a .fa-facebook {
    color: #3b5998;
}

ul.footer_social li a .fa-instagram {
    color: #8a3ab9;
}

ul.footer_social li a .fa-youtube {
    color: #ff0000;
}

ul.footer_social li a .i:hover {
    color: #ec4090 !important;
}

/* Footer Contact */
p.contact-info {
    margin-bottom: 8px;
}

p.contact-info a {
    margin-bottom: 8px;
    color: #7c8595;
}

p.contact-info a:hover {
    text-decoration: underline;
}

p.contact-info .i {
    font-size: 18px;
    padding-right: 5px;
    vertical-align: middle;
    color: #1e90ff;
}

.scrollTop {
    position: fixed;
    width: 100%;
    bottom: 20px;
    align-items: center;
    height: 20px;
    justify-content: center;
    z-index: 1000;
    color: #EC4090;
    cursor: pointer;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
    right: 20px;
}

.scrollTop:hover {
    opacity: 1;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0.5;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0.5;
    }
}

